import { useEffect, useState } from 'react';
import { chartOptions } from './helpers';
import './styles.css';
import ReactApexChart from 'react-apexcharts';
import { makeRequest } from '../../utils/request';
import { IndicadorByDate } from '../../types';

const initialData = [
  {
    x:'2023-09-02',
    y:45
  },
  {
    x:'2023-09-03',
    y:1150
  },
  {
    x:'2023-09-04',
    y:2059
  },
  {
    x:'2023-09-05',
    y:1505
  }
]
function IndicatorsByDate() {
  const [indicatorsByDate, setIndicatorsByDate]=useState<IndicadorByDate[]>([]);
  useEffect(()=>{
   makeRequest.get<IndicadorByDate[]>('http://177.84.88.122:8080/indicadores/CANAMOISA').then(response =>{
    const newIdicator = response.data;
    setIndicatorsByDate(newIdicator);
    console.log (response.data);
   })
  },[])
  return (
    <div className='indicators-by-date-container card-base'>
      <div >
         <h4 className='indicators-by-date-title'>
            Evoluo de Safra
         </h4>
         <span className='indicators-by-date-period'>02/09/2023 a 31/04/2024</span>
         <div className='indicators-by-date-data'>
           <div className='indicators-by-date-quantity-container'>
              <h2 className='indicators-by-date-quantity'>
              {indicatorsByDate[0]?.valor}
              </h2>
              <span className='indicators-by-date-quantity-label'>
              {indicatorsByDate[0]?.descricao}
              </span>
              <span className='indicators-by-date-quantity-description'>
                 O Grafico Mostra a Evoluo da Safra
              </span>
           </div>
           <div className='indicators-by-date-charts'>
             <ReactApexChart
                 options={chartOptions}
                 series={[{name:'Moagem',
                 data:initialData}]}
                 type='bar'
                 height={240}
                 width="100%"
                 />
           </div>
         </div>
      </div>
    </div>
  )
}

export default IndicatorsByDate
