import './styles.css';

type Props = {
  label: string;
  value: number;
 // icon: React.ReactNode;
}

function IndicatorsSumaryCard({value,label}:Props) {
  return (
    <div className='indicators-sumary-card card-base'>
      <h3 className='indicators-sumary-value'>{value}</h3>
      <h3 className='indicators-sumary-description'>{label}</h3>
    </div>
  )
}

export default IndicatorsSumaryCard;
