import { useEffect, useState } from 'react';
import { IndicadorBySumary, IndicadorBySumaryAtr, IndicadorBySumaryEstoque } from '../../types';
import IndicatorsSumaryCard from './indicators-sumary-card';
import './styles.css';
import { makeRequest } from '../../utils/request';

function IndicatorsSumary() {
  const [indicatorsBySumary, setIndicatorsBySumary]=useState<IndicadorBySumary[]>([]);
  useEffect(()=>{
   makeRequest.get<IndicadorBySumary[]>('http://177.84.88.122:8080/indicadores/CANAMOI').then(response =>{
    const newIdicator = response.data;
    setIndicatorsBySumary(newIdicator);
    console.log (response.data);
   })
  },[]);
  const [indicatorsBySumaryEstoque, setIndicatorsBySumaryEstoque]=useState<IndicadorBySumaryEstoque[]>([]);
  useEffect(()=>{
   makeRequest.get<IndicadorBySumaryEstoque[]>('http://177.84.88.122:8080/indicadores/ESTOQUEP').then(response =>{
    const newIdicatorEst = response.data;
    setIndicatorsBySumaryEstoque(newIdicatorEst);
    console.log (response.data);
   })
  },[]);
  const [indicatorsBySumaryAtr, setIndicatorsBySumaryAtr]=useState<IndicadorBySumaryAtr[]>([]);
  useEffect(()=>{
   makeRequest.get<IndicadorBySumaryAtr[]>('http://177.84.88.122:8080/indicadores/RENDDA').then(response =>{
    const newIdicatorAtr = response.data;
    setIndicatorsBySumaryAtr(newIdicatorAtr);
    console.log (response.data);
   })
  },[]);
  return (
    <div className='indicators-sumary-container'>
        <IndicatorsSumaryCard value={indicatorsBySumary[0]?.valor} label={indicatorsBySumary[0]?.descricao}/>
        <IndicatorsSumaryCard value={indicatorsBySumaryEstoque[0]?.valor} label ={indicatorsBySumaryEstoque[0]?.descricao}/>
        <IndicatorsSumaryCard value={indicatorsBySumaryAtr[0]?.valor} label={indicatorsBySumaryAtr[0]?.descricao}/>
        <IndicatorsSumaryCard value={390.450} label='Estoque'/>
    </div>
  )
}

export default IndicatorsSumary;
