import './App.css';
import Filter from './components/filter';
import Header from './components/header';
import IndicatorsByDate from './components/indicators-by-date';
import IndicatorsSumary from './components/indicators-sumary';

function App() {
  return (
    <>
    <Header/>
    <div className='app-container'>
      <Filter />
     <IndicatorsByDate/>
     <div className='indicators-overview-container'>
          <IndicatorsSumary/>
     </div>
    </div>
    </>
  );
}

export default App;
